"use client";

import { acceptFollowRequest, declineFollowRequest } from "@/lib/actions";
import Image from "next/image";
import { useOptimistic, useState } from "react";
const FriendRequestList = ({
  requests
}) => {
  const [requestState, setRequestState] = useState(requests);
  const accept = async (requestId, userId) => {
    removeOptimisticRequests(requestId);
    try {
      await acceptFollowRequest(userId);
      setRequestState(prev => prev.filter(req => req.id !== requestId));
    } catch (err) {
      console.log(err);
    }
  };
  const decline = async (requestId_0, userId_0) => {
    removeOptimisticRequests(requestId_0);
    try {
      await declineFollowRequest(userId_0);
      setRequestState(prev_0 => prev_0.filter(req_0 => req_0.id !== requestId_0));
    } catch (err_0) {
      console.log(err_0);
    }
  };
  const [optimisticRequests, removeOptimisticRequests] = useOptimistic(requestState, (state, value) => state.filter(req_1 => req_1.id !== value));
  return <div className="">
        {optimisticRequests.map(request => <div className="flex items-center justify-between" key={request.id}>
            <div className=" flex items-center gap-4">
                <Image src={request.sender.avatar || "/noAvatar.png"} alt="" width={40} height={40} className="w-10 h-10 rounded-full object-cover" />
                <span className="font-semibold"> {request.sender.name ? request.sender.name + "" : request.sender.username}</span>
            </div>
            <div className="flex gap-3 justify-end">
                <form action={() => accept(request.id, request.sender.id)}>
                    <button>
                    <Image src="/accept.png" alt="" width={20} height={20} className="cursor-pointer" />
                    </button>
                </form>
                <form action={() => decline(request.id, request.sender.id)}>
                    <button>
                    <Image src="/reject.png" alt="" width={20} height={20} className="cursor-pointer" />
                    </button>
                </form>

            </div>
            
        </div>)}

        </div>;
};
export default FriendRequestList;